@import "styles/variables.scss";


.content {
    display: flex;
    flex-direction: column;
    gap: 80px;
    text-align: center;
    padding-inline: 80px;
    padding-bottom: 428px;
}

.middle {
    display: flex;
    gap: 191px;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    width: 100vw;
}

.middle_text {
    display: flex;
    flex-direction: column;
    gap: 64px;
    align-items: self-start;
    justify-content: flex-start;

    .text {
        display: flex;
        flex-direction: column;
        text-align: start;
        gap: 32px;
    }
}

.second_middle {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lookbook {
    display: flex;
    flex-direction: column;
    gap: 64px;
    margin-bottom: 48px;
}

.title {
    font-size: 48px;
    font-weight: 450;
    line-height: 56px;
    letter-spacing: 0em;
    text-align: center;
}

.collection {
    font-size: 24px;
    font-weight: 800;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: center;
    margin-bottom: 48px;
}

.third_middle {
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 64px;
    margin-bottom: 48px;
    text-align: center;

    section {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 24px;
    }

    div {
        display: flex;
        align-items: center;
        gap: 128px;
        justify-content: center;
        width: 100%;

        .advantage {
            display: flex;
            flex-direction: column;
            gap: 16px;
            max-width: 472px;

            .adv_styles {
                font-size: 40px;
                font-weight: 450;
                line-height: 52px;
                letter-spacing: 0em;
                text-align: center;

            }
        }
    }
}

.fivth_middle {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .two_friends {
        display: flex;
        gap: 274px;
        justify-content: center;

        span {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 24px;
        }
    }
}

.button_catalog {
    width: 300px;
}

.fourth_middle {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 64px;
    width: 100%;
}

@media #{$tablet} {

    .bg {
        background-color: #F6F7F8;
        min-height: 360px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }

    .bread_kids {
        padding-top: 40px;
        padding-left: 20px;
        cursor: pointer;
        padding-bottom: 30px;
        font-size: 16px;
        font-weight: 300;
        line-height: 21px;
        align-self: self-start;
        letter-spacing: 0em;
        text-align: left;
    }


    .fourth_middle {
        gap: 32px;
    }

    .button_kid {
        margin-left: 20px;
        margin-bottom: 20px;
        align-self: self-start;
        width: 90%;
    }

    .about_kids {
        display: flex;
        flex-direction: column;
        gap: 4px;
        align-self: self-start;
        padding-left: 20px;

        .title_text {
            font-size: 24px;
            width: 100%;
            font-weight: 800;
            line-height: 32px;
            letter-spacing: 0em;
        }

        .bottom_text {
            font-size: 16px;
            font-weight: 300;
            line-height: 24px;
            letter-spacing: 0em;
        }
    }

    .content {
        display: flex;
        flex-direction: column;
        text-align: center;
        gap: 60px;
        padding-inline: 10px;
        padding-bottom: 688px;
    }

    .middle {
        display: flex;
        flex-direction: column;
        gap: 48px;
        align-items: center;
        height: 100%;
        width: 100%;
        transform: translateX(-10px);

        .middle_banner {
            transform: translateX(2%);
        }
    }

    .middle_text {
        display: flex;
        flex-direction: column;
        gap: 40px;
        width: 100%;
        margin-left: 40px;
        align-items: self-start;

        .text {
            p {
                font-size: 16px;
                font-weight: 300;
                line-height: 24px;
                letter-spacing: 0em;
            }

            display: flex;
            flex-direction: column;
            text-align: start;
            gap: 16px;
        }
    }

    .second_middle {
        display: flex;
        flex-direction: column;
        align-items: center;
    }


    .lookbook {
        display: flex;
        flex-direction: column;
        gap: 64px;
        margin-bottom: 48px;
    }

    .title {
        font-size: 24px;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: center;
    }

    .descr {
        width: 100%;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0em;
        color: black;
    }

    .big_middle {
        display: flex;
        flex-direction: column;
        width: 100vw;
        transform: translateX(-10px);
    }

    .orient {
        align-self: center;
        font-size: 24px;
        font-weight: 800;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: center;
        margin-bottom: 32px;
    }

    .orient_latin {
        align-self: center;
        font-size: 24px;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: center;
        margin-bottom: 32px;
    }

    .button {
        width: 90% !important;
    }

    .third_middle {
        display: flex;
        width: 100%;
        flex-direction: column;
        gap: 64px;
        margin-bottom: 0px;
        text-align: center;

        section {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 24px;
        }

        div {
            display: flex;
            align-items: center;
            flex-direction: column;
            gap: 32px;
            justify-content: center;
            width: 100%;

            .advantage {
                display: flex;
                flex-direction: column;
                gap: 16px;
                max-width: 472px;

                .adv_styles {
                    font-size: 24px;
                    width: 100%;
                    white-space: nowrap;
                    text-wrap: nowrap;
                    font-weight: 800;
                    line-height: 31px;
                    letter-spacing: 0em;
                    text-align: center;
                }
            }
        }
    }

    .fivth_middle {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        .two_friends {
            display: flex;
            flex-direction: column;
            gap: 48px;
            justify-content: center;

            span {
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                gap: 24px;
            }
        }
    }

}