@import "styles/variables.scss";

.novelties {
    display: flex;
    flex-wrap: wrap;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    border-left: 1px solid rgba(35, 35, 35, 0.3);
}

.noPadding {
    padding-bottom: 0px;
    margin-bottom: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.borderTop>div {
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.button {
    margin: 60px 0 0;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}

@media #{$tablet} {
    .noveltiesCol {
        margin-bottom: 80px !important;
    }

    .novelties {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .noPadding {
        padding-bottom: 0px;
        margin-bottom: 0px !important;
    }

    button.button {
        width: 100%;
        margin-top: 32px;
    }
}